
.IroHandle > :nth-child(1) {
    stroke-width: 1;
    stroke: #000;
}

.IroHandle > :nth-child(2) {
    stroke-width: 2;
    stroke: #fff;
}

.swatchbook-colour-picker > * {
    background: white;
    box-shadow: 0 0.25rem 0.75rem 0rem hsla(0,0%,20%,0.1);
}

.swatchbook-colour-picker > .IroColorPicker {
    position: absolute;
}

.swatchbook-listing-colour-swatch-background {
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    margin: 0.5em;

    width: 2.5rem;
    height: 2.5rem;
    border-radius: 1.25rem;

    background-color: lightgray;
    background-image:  repeating-linear-gradient(45deg, gray 25%, transparent 25%, transparent 75%, gray 75%, gray), repeating-linear-gradient(45deg, gray 25%, lightgray 25%, lightgray 75%, gray 75%, gray);
    background-position: 0 0, 10px 10px;
    background-size: 20px 20px;

}

.swatchbook-listing-colour-swatch {
    position: relative;
    display: inline-block;
    width: 2.5rem;
    height: 2.5rem;
    border: 1px solid gray;
    border-radius: 1.25rem;

    vertical-align: middle;
    margin: 0.5em;

}

.swatchbook-colour-swatch-panel {
    position: relative;
    margin: 1em;
}

.swatchbook-colour-swatch {

    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 1.25rem;
    border: 2px solid gray;
}

.swatchbook-colour-swatch-background {
    position: absolute;
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 1.25rem;

    background-color: lightgray;
    background-image:  repeating-linear-gradient(45deg, gray 25%, transparent 25%, transparent 75%, gray 75%, gray), repeating-linear-gradient(45deg, gray 25%, lightgray 25%, lightgray 75%, gray 75%, gray);
    background-position: 0 0, 10px 10px;
    background-size: 20px 20px;
}

.swatchbook-colour-picker-panel {

    width: 100%;
    display: none;
    flex-flow: row nowrap;
    align-items: center;
    padding: 1rem;
    margin-top: 0.25rem;
    border: solid 1px lightgray;
    border-radius: 0.25rem;
}

.swatchbook-colour-picker-manual-control {

    position: relative;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    padding: 0.2rem;
}

.swatchbook-colour-picker-manual-control > label,
.swatchbook-colour-picker-manual-control > input[type="number"] {
    display: inline-block;
    vertical-align: middle;
    font-size: 0.75rem;
    padding: 0.2rem;
    min-height: 1rem;
}
